@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap");*{box-sizing:border-box;margin:0;padding:0}body{background:#333;color:#fff;font-family:"Roboto", sans-serif}h2{font-size:45px;font-weight:300;margin:10px}span{font-size:30px}p{font-size:20px}.container{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:10px}.container>div{height:210px;background-size:cover;background-attachment:fixed;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;cursor:pointer;transition:all 0.2s ease}.container>div:hover{opacity:0.7;transform:scale(0.98)}.container>div:nth-of-type(1){grid-column:1/3}.container>div:nth-of-type(6){grid-column:3/5}.container>div:nth-of-type(9){grid-column:3/5}.container>div:nth-of-type(10){grid-column:1/3}.bg1{background:url("../assets/img/bg1.jpg")}.bg2{background:url("../assets/img/bg2.jpg");color:#333}@media (max-width: 960px){.container{grid-template-columns:repeat(3, 1fr)}.container>div:nth-of-type(1){grid-column:1/2}.container>div:nth-of-type(6){grid-column:3/4}.container>div:nth-of-type(9){grid-column:3/4}.container>div:nth-of-type(10){grid-column:1/2}}@media (max-width: 820px){.container{grid-template-columns:repeat(2, 1fr)}}@media (max-width: 690px){.container{display:flex;flex-direction:column;flex-wrap:wrap}.container>div{margin-bottom:10px}}
